    display: flex;
    align-items: center;
    gap: 0 .5em;
    position: relative;
    cursor: pointer;
    top: 3px;
  }
  .checkbox label::before {
    width: 18px;
    height: 18px;
    border-radius: 3px;
    border: 2px solid #d6dde3;
    content: '';
  }
  .checkbox label:has(:checked)::after {
    position: absolute;
    top: 5px;
    left: 7px;
    transform: rotate(45deg);
    width: 5px;
    height: 10px;
    border: solid #2589d0;
    border-width: 0 3px 3px 0;
    content: '';
  }
  .checkbox input {
    display: none;
  }

  /* -----------------------------------------*/
  /* 表示：更新日 */
  div.version {
    text-align: left;
    padding-left: 5px;
  }

  /* -----------------------------------------*/
  /* テーブル：値段指定 */
  table.inputTable {
    border: solid 1px black; /* 枠線指定 */
    padding: 10px; /* 余白指定 */
    border-collapse:  collapse; /* セルの線を重ねる */
    background-color: #dadada;
  }
  th.inputTitle {
    border-right: 1px solid #999;
    border-bottom: 2px solid #999;
    background: #2589d0;
    position: sticky; /* ヘッダー固定 */
    top: 0;
    left: 0;
    color: #ffffff;
    /* border-bottom: solid 3px #9c9c94; */
  }
  td.inputItemHeadline {
    white-space: nowrap; /* 折り返しなし */
    vertical-align: top;
    text-align: left;
  }
  td.inputItemTitle {
    white-space: nowrap; /* 折り返しなし */
    vertical-align: top;
    text-align: right;
  }

  /* -----------------------------------------*/
  /* テーブル：パックリスト */
  table.outputTable{
    width: 100%;
  }
  tr.outputTopic{
    background-color: rgb(224, 245, 255)
  }
  .hover tr:hover {
    background: #93efff;
  }
  td.top {
    vertical-align: top;
  }
  td.filterSelect {
    text-align: right;
  }
  .packList td.id {
    min-width: 30px;
  }
  .packList td.division {
    min-width: 120px;
  }
  .packList td.name {
    min-width: 120px;
  }
  .packList td.price {
    min-width: 50px;
  }
  .packList td.degree {
    min-width: 30px;
  }
  .packList td.Contents {
    min-width: 100px;
  }
  .packList{
    overflow: auto;
    width: 100%;
    height: 902px; /* ヘッダー固定に必要なテーブル縦幅指定 */
  }
  .packList table{
    margin: 0;
    border-spacing: 0;
  }
  .packList th{
    border-right: 1px solid #999;
    border-bottom: 1px solid #999;
    background: #2589d0;
    position: sticky; /* ヘッダー固定 */
    top: 0;
    left: 0;
    color: #ffffff;
  }
  .packList td{
    border-right: 1px solid #999;
    border-bottom: 1px solid #999;
    padding: 5px;
  }
  .packList td.Num{
    white-space: nowrap; /* 折り返しなし */
    margin-left: auto;
    text-align: right;
  }
  .packList td.Str {
    /*white-space: nowrap; /* 折り返しなし */
    text-align: left;
  }
  .packList td.Str-Wrap {
    text-align: left;
  }
  .asc::after {
    content: "↓"
  }
  .desc::after {
    content: "↑"
  }
</style>



